.container{
    position: relative;
    width: 100%;
    padding: 0;
    .content{
        position: relative;
        text-align: center;
        cursor: pointer;
        &:hover img{
            transform: scale(1.025);
        }
        div{
            border-radius: 8px;
            overflow: hidden;
            display: flex;
            justify-content: center;
            img{
                width: 100%;
                display: block;
                transition: 0.2s linear;
                border-radius: 8px;
            }
        }
        .text{
            color: #fff;
            position: absolute;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            bottom: 0;
            margin-bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
            max-width: 100%;
            h1{
                font-family: 'Oswald', sans-serif;
                font-size: 2.2rem;
            }
            span{
                font-size: 1.1rem;
            }
        }
    }
}