.card{
    max-width: 320px;
    &:hover img{
        transform: scale(1.05);
    }
    .img{
        overflow: hidden;
        width: 100%;
        max-width: 320px;
        border-radius: 8px;
        cursor: pointer;
        img{
            max-width: 320px;
            transition: 0.2s linear all;
        }
    }
    .content{
        margin-top: 8px;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        h3{
            font-size: 1.25rem;
            cursor: pointer;
        }
    }
}